import React from 'react'
import { Map, TileLayer, Marker, Popup } from 'react-leaflet'
import './simpleleafletmap.css'

class SimpleLeafletMap extends React.Component {
  constructor(props) {
    //Set some default properties
    if (typeof props.position == 'undefined') {
      props.position = [51, -1];
    }
    if (typeof props.zoom == 'undefined') {
      props.zoom = 13;
    }
    super(props);
  }

  render() {

    if (typeof window !== 'undefined') {
      return (
        <Map center={this.props.position} zoom={this.props.zoom}>
          <TileLayer
            url="http://{s}.tile.osm.org/{z}/{x}/{y}.png"
            attribution="&copy; <a href=&quot;http://osm.org/copyright&quot;>OpenStreetMap</a> contributors"
          />
          {typeof this.props.markerText !== 'undefined' &&
          <Marker position={this.props.position}>
            <Popup>{this.props.markerText}</Popup>
          </Marker>
          }
        </Map>
      );
    }
    return null
  }
}

export default SimpleLeafletMap
